<template>
  <div class="bodyPart">
    <ul>
      <li class="unit" v-for="(item, index) in dataList" :key="index">
        <div class="itemPer">
          <div class="cover" @click="clickEvent(item.title)"></div>
          <iframe
            :src="item.cover"
            class="frame"
            border="0"
            :title="item.title"
          ></iframe>
          <span>{{ item.title }}</span>
        </div>
      </li>
    </ul>

    <div><span>加载更多</span></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      page_start: "0",
      page_limit: "20",
      tag: "热门",
      type: "movie",
      dataList: [],
    };
  },
  mounted() {
    this.getHotMovieList();
  },
  methods: {
    clickEvent(val) {
      alert(val);
    },
    // 获取热门电影列表
    getHotMovieList() {
      this.$http.get("/img3/p2634040881.jpg").then((res) => {
        console.log(res);
      });
      var url = `/api/j/search_subjects?type=movie&tag=${this.tag}&sort=time&page_limit=${this.page_limit}&page_start=${this.page_start}`;
      this.$http
        .get(url)
        .then((response) => {
          console.log(response);
          this.dataList = response.data.subjects;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.bodyPart {
  width: 100%;
  ul {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    li {
      list-style: none;
      height: 450px;
      .itemPer {
        height: 100%;
        position: releative;
        .cover {
          position: absolute;
          width: 300px;
          height: 400px;
          cursor: pointer;
          z-index: 2;
        }
      }
      iframe {
        height: 410px;
        border: 0px;
        overflow: hidden;
      }
      span {
        display: block;
      }
    }
  }
}
</style>


